        ul.programmes__filter
            li.programmes__filter-item
                label.label__filter-teacher(for='subjects') Выберите предмет:
                select(id='subjects' name='subjects' v-model="selectedSubject").programmes__filter-select
                    option(value='') Все
                    option(:value='item' v-for='item in subjects') {{item}}
            li.programmes__filter-item
                label.label__filter-teacher(for='classes') Выберите класс:
                select(name='classes' v-model="selectedClass").programmes__filter-select
                    option(:value='0') Все
                    option(:value='item' v-for='item in 12') {{item}}
            li.programmes__filter-item
                label.label__filter-teacher(for='languages') Выберите язык:
                select(name='languages' v-model="selectedLanguage").programmes__filter-select
                    option(value='') Все
                    option(value='Казахский') Казахский
                    option(value='Русский') Русский
        ul.programmes__list
            li.programmes__item(v-for="item,idx in filteredProgrammes" :key="item.id")
                programme-item(
                    :index='idx'
                    :programme='item'
                )
        button(@click.prevent='addProgramme' id='addProgramme').add__programme Добавить программу
        add-programme-form(
            v-if='addProgrammeFormVisible'
            v-on:eventNewProgramme='eventNewProgramme'
            )
</template>
