            'Жусупова Динара',
            'Таспаганбетова Жанар'
        ],
        subjects: [
            'Математика',
            'Логика',
            'Қазақ тілі',
            'География'
        ],
        selectedAuthor: '',
        selectedLanguage: '',
        selectedSubject: '',
        selectedClass: 0,
        addProgrammeFormVisible: false,
        programmePointsVisible: false,
        programmes: [
            {
                programmeName: 'Логика-5',
                programmeAuthor: 'Жусупова Гульнара',
                programmeSubject: 'Логика',
                programmeClass: 5,
                programmeLanguage: 'Русский',
                programmeDepartment: 'Естественно-технический',
                programmePoints: [
                    {
                        pointName: 'Множества',
                        pointDesc: 'Операции со множествами: объединение, пересечение',
                        pointDuration: 2
                    }, 
                    {
                        pointName: 'Ханойская башня',
                        pointDesc: 'Математическая индукция, доказательство утверждений',
                        pointDuration: 5
                    },
                    {
                        pointName: 'Раскрашивания',
                        pointDesc: 'Игра Танграм, пентамино',
                        pointDuration: 3
                    }
                ]
            },
            {
                programmeName: 'Математика-5',
                programmeAuthor: 'Таспаганбетова Жанат',
                programmeSubject: 'Математика',
                programmeClass: 5,
                programmeLanguage: 'Казахский',
                programmeDepartment: 'Естественно-технический',
                programmePoints: [
                    {
                        pointName: 'Общие множители',
                        pointDesc: 'Разложение на произведение простых чисел, приведение к общему знаменателю',
                        pointDuration: 4
