                    {
                        pointName: 'Операции с простыми дробями',
                        pointDesc: 'Арифметические операции с простыми дробями: сложение, вычитание, умножение и деление',
                        pointDuration: 4
                    },
                    {
                        pointName: 'Алгоритм Евклида',
                        pointDesc: 'Алгоритм Евклида',
